import Head from 'next/head'
import { useState, useEffect } from 'react'

import Carousel from '../components/Carousel'
import Gallery from '../components/Gallery'

import '../styles/app.styl'
import '../public/css/fontello.css'

import me from '../me.json'
const nav = ['contact', 'work', 'about']

export default function Folio() {
  const [active, setActive] = useState(nav[0])
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false)
  const [galleryOpen, setGalleryOpen] = useState(false)
  const [activeImageIndex, setActiveImageIndex] = useState(0)

  function segment() {
    const noop = () => undefined
    return window && window.analytics || {track: noop, identify: noop, page: noop, trackLink: noop}
  }

  useEffect(() => {
    segment().identify()
  }, [])
  useEffect(() => {segment().page(active)}, [active])
  useEffect(() => {document.querySelectorAll('a').forEach(link => {
    segment().trackLink(link, `Contact Link Clicked - ${link.className}`, {href: link.href})
  })},[])

  return (
    <div>
      <Head>
        <title>{me.title}</title>
        <link rel="icon" href="/favicon.svg" />

        <script
          dangerouslySetInnerHTML={{
            __html: `
    !function(){var analytics=window.analytics=window.analytics||[];if(!analytics.initialize)if(analytics.invoked)window.console&&console.error&&console.error("Segment snippet included twice.");else{analytics.invoked=!0;analytics.methods=["trackSubmit","trackClick","trackLink","trackForm","pageview","identify","reset","group","track","ready","alias","debug","page","once","off","on","addSourceMiddleware","addIntegrationMiddleware"];analytics.factory=function(e){return function(){var t=Array.prototype.slice.call(arguments);t.unshift(e);analytics.push(t);return analytics}};for(var e=0;e<analytics.methods.length;e++){var t=analytics.methods[e];analytics[t]=analytics.factory(t)}analytics.load=function(e,t){var n=document.createElement("script");n.type="text/javascript";n.async=!0;n.src="https://cdn.segment.com/analytics.js/v1/"+e+"/analytics.min.js";var a=document.getElementsByTagName("script")[0];a.parentNode.insertBefore(n,a);analytics._loadOptions=t};analytics.SNIPPET_VERSION="4.1.0";
    analytics.load("t5QSvw4fH0VfJgLCnB00nHfxo8vfGV3T");
    analytics.page();
    }}();
    `,
          }}
        />
      </Head>

      <h1>{me.name}</h1>
      <div className="mobile">
        <div
          className="button"
          onClick={() => {
            segment().track('Mobile Menu Clicked', {active, mobileMenuOpen})
            setMobileMenuOpen(!mobileMenuOpen)
          }}
        >
          <i className="icon-menu"></i>
        </div>
        <div className="name">{me.name}</div>
      </div>
      <div id="app">
        <div className="overlay"></div>
        <div className={`navigation ${mobileMenuOpen ? 'open' : ''}`}>
          <ul>
            <span className={`slider ${active}`}></span>
            {nav.map(view => (
              <li
                key={view}
                onClick={() => {
                  segment().track(`Mobile Nav Link Clicked - ${view}`, {active, mobileMenuOpen})
                  setActive(view)
                  setMobileMenuOpen(false)
                }}
                onMouseEnter={() => {
                  segment().track('Nav MouseEnter', {view})
                  !mobileMenuOpen && setActive(view)
                }}
              >
                {view}
              </li>
            ))}
          </ul>
        </div>
        <div className="main">
          <div className="mobile-subheader">{active}</div>
          <div className="circle"></div>
          <div className={`spinner ${active}`}>
            {nav.map(view => (
              <div
                key={view}
                className={`button ${view}`}
                onClick={() => {
                  segment().track(`Spinner Button Clicked - ${view}`, {active})
                  setActive(view)
                }}
              >
                <div className="info">
                  <div>{view}</div>
                </div>
              </div>
            ))}
          </div>
          <div className="wrapper">
            <div className="contents">
              <div
                className={`content contact ${
                  active === 'contact' && 'active'
                }`}
              >
                {me.contacts.map(contact => (
                  <div key={contact.icon}>
                    <a
                      className={contact.icon}
                      href={contact.url}
                      target="_blank"
                    >
                      <i className={`icon-${contact.icon}`}></i>
                      {contact.icon === 'file-pdf' && (
                        <div className="resume">Resume</div>
                      )}
                    </a>
                  </div>
                ))}
              </div>
              <div className={`content work ${active === 'work' && 'active'}`}>
                <Carousel
                  work={me.work}
                  activeImageIndex={activeImageIndex}
                  setActiveImageIndex={setActiveImageIndex}
                  setGalleryOpen={setGalleryOpen}
                  segment={segment}
                />
              </div>
              <div
                className={`content about ${active === 'about' && 'active'}`}
              >
                <img src="/images/me.png" />
                <div>{me.about.bio}</div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <Gallery
        work={me.work}
        activeImageIndex={activeImageIndex}
        setActiveImageIndex={setActiveImageIndex}
        galleryOpen={galleryOpen}
        setGalleryOpen={setGalleryOpen}
        segment={segment}
      />
    </div>
  )
}
